<template>
  <div class="index">
    <Layout>
      <Header :style="{height: '10vh', lineHeight: '10vh'}">
        <div class="index-title">
          云南旅游景点后台管理系统
          <Icon :style="{cursor: 'pointer'}" :title="'退出登录'" @click="logout()" type="md-exit" />
          <div class="user-info">
            <Icon :style="{cursor: 'pointer'}" :title="'去展示页'" @click="goShow()" type="md-swap" />
          </div>
        </div>
      </Header>
      <Layout :style="{height: '80vh'}">
        <Sider hide-trigger :style="{background: '#fff', height: '100%', overflowY: 'auto'}">
          <Menu ref="menu"
                :active-name="activeName"
                theme="light"
                width="auto"
                :open-names="[activeType]"
                :accordion="true"
                @on-select="menuSelect">
            <Submenu :name="item.type" v-for="(item, index) of types" :key="index">
              <template slot="title">
                <Icon type="md-list" />
                {{item.type}}
              </template>
              <MenuItem :name="i" v-for="(i, index) of item.name" :key="index">{{i}}</MenuItem>
            </Submenu>
          </Menu>
        </Sider>
        <Layout :style="{padding: '0 24px'}">
          <Breadcrumb :style="{margin: '24px 0'}">
            <BreadcrumbItem>景点</BreadcrumbItem>
            <BreadcrumbItem>{{activeType}}</BreadcrumbItem>
            <BreadcrumbItem>{{activeName}}</BreadcrumbItem>
          </Breadcrumb>
          <Content :style="{padding: '24px', minHeight: '280px', background: '#fff', overflowY: 'auto'}">
            <item :active="activeName" @refresh="refresh" v-if="activeName"></item>
          </Content>
        </Layout>
      </Layout>
      <Footer :style="{textAlign: 'center', height: '10vh'}">
        2019 @ Yunnan Tourists
      </Footer>
    </Layout>
  </div>
</template>

<script>
  import Item from '@/components/Item'
  import axios from 'axios'
  export default {
    name: "Index",
    components: {
      Item
    },
    data() {
      return {
        types: [], // 树形列表数据
        activeName: '', // 当前选择的景点名称
        activeType: '' // 当前选择的景点类型
      }
    },
    methods: {
      menuSelect(name) {
        this.activeName = name
        this.types.map((val) => {
          if (val.name.indexOf(name) > -1) {
            this.activeType = val.type
            return
          }
        })
      },
      logout() {
        this.$Modal.confirm({
          title: '是否确认退出登录？',
          loading: true,
          onOk: () => {
            this.goLogout()
          }
        })
      },
      goLogout() {
        axios.post('/user/logout', {})
          .then((res) => {
            const data = res.data
            if (data.code === 70000) {
              this.$Modal.remove()
              this.$Notice.success({
                title: '退出登录！'
              })
              this.$router.push('/login')
            }
          })
      },
      goShow() {
        this.$Modal.confirm({
          title: '是否跳转到展示页？',
          onOk: () => {
            this.$router.push('/show')
          }
        })
      },
      getTypes(name) {
        axios.post('/travel/getTravelTree', {})
          .then((res) => {
            const data = res.data
            if (data.code === 70000) {
              this.types = data.data
              if (name) {
                this.activeName = name
                this.types.map((val) => {
                  if (val.name.indexOf(name) > -1) {
                    this.activeType = val.type
                  }
                })
              } else {
                this.activeType = this.types[0].type
                this.activeName = this.types[0].name[0]
              }
              this.$nextTick(() => {
                this.$refs.menu.updateOpened()
                this.$refs.menu.updateActiveName()
              })
            } else if (data.code === 70007) {
              this.$Notice.warning({
                title: '对不起，请先登录！'
              })
              this.$router.push('/login')
            }
          })
      },
      refresh(name) {
        if (name) {
          this.getTypes(name)
        } else {
          this.getTypes()
        }
      }
    },
    mounted() {
      this.getTypes()
    }
  }
</script>

<style lang="stylus" scoped>
.index >>> .ivu-layout-sider::-webkit-scrollbar
  display none
.index >>> .ivu-layout-content::-webkit-scrollbar
  display none
.index
  position relative
  overflow hidden
  background #f5f7f9
  .index-title
    color #eee
    font-size 24px
  .user-info
    display inline-block
    float right
</style>



