import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

let types = []
try {
  if (localStorage.getItem('touristType')) {
    types = JSON.parse(localStorage.getItem('touristType'))
  } else {
    axios.post('/travel/queryTravelType', {})
      .then((res) => {
        const data = res.data
        if (data.code === 70000) {
          types = data.data
          localStorage.setItem('touristType', JSON.stringify(types))
        }
      })
  }
} catch (e) {

}

export default new Vuex.Store({
  state: {
    types
  }
})



