<template>
  <div class="login">
    <Layout>
      <Header :style="{height: '10vh', lineHeight: '10vh'}">
        <div class="login-title">
          云南旅游景点
        </div>
      </Header>
      <Content :style="{height: '80vh', position: 'relative'}">
        <Card :style="{width: '300px', height: '350px', position: 'absolute', top: '10vh', right: '200px', overflow: 'hidden'}">
          <p slot="title" style="text-align: center; color: #2d8cf0; font-size: 16px;">账号登录</p>
          <Input prefix="ios-contact" v-model="userName" placeholder="账号" clearable style="width: auto; margin: 30px;" />
          <Input type="password" prefix="md-lock" v-model="password" placeholder="密码" clearable style="width: auto; margin: 16px 30px 46px;" />
          <Button type="info" style="margin-left: 30px; width: 70%;" @click="login">登 录</Button>
        </Card>
      </Content>
      <Footer :style="{height: '10vh', textAlign: 'center'}">
        2019 @ Yunnan Tourists
      </Footer>
    </Layout>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "Login",
    data() {
      return {
        userName: '',
        password: ''
      }
    },
    methods: {
      goLogin(params) {
        axios.post('/user/login', params)
          .then((res) => {
            const data = res.data
            if (data.code === 70000) {
              this.$Notice.success({
                title: '登录成功！'
              })
              this.$router.push('/index')
            } else {
              this.$Notice.error({
                title: data.msg
              })
            }
          })
      },
      login() {
        if (this.userName && this.password) {
          let params = {
            userName: this.userName,
            password: this.password
          }
          this.goLogin(params)
        } else {
          this.$Notice.warning({
            title: '用户名或密码不能为空！'
          })
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
.login
  position relative
  overflow hidden
  background #f5f7f9
  .login-title
    color #eee
    font-size 24px
</style>



