<template>
  <div class="item">
    <Row :gutter="24">
      <Col span="12">
        <label class="label">名称：</label>
        <Input v-if="isEdit" :style="{width: 'auto'}" v-model="form.name" placeholder="名称..." clearable />
        <span v-else>{{data.name}}</span>
      </Col>
      <Col span="8">
        <label class="label">类型：</label>
        <Select v-if="isEdit" v-model="form.type" :style="{width: 'auto'}" clearable>
          <Option :value="item.name" v-for="item of types" :key="item.type">{{item.name}}</Option>
        </Select>
        <span v-else>{{data.type}}</span>
      </Col>
      <Col span="4" :style="{textAlign: 'right'}">
        <template v-if="isEdit">
          <Button size="small" type="info" @click="save()">保存</Button>
          <Button size="small" type="warning" @click="cancel()">取消</Button>
        </template>
        <template v-else>
          <Button size="small" type="info" @click="edit()">编辑</Button>
          <Button size="small" type="info" @click="add()">新增</Button>
          <Button size="small" type="warning" @click="remove()">删除</Button>
        </template>
      </Col>
    </Row>
    <br>
    <Row :gutter="24">
      <Col span="12">
        <label class="label">等级：</label>
        <RadioGroup v-if="isEdit" v-model="form.travelLevel" type="button">
          <Radio :label="1">A</Radio>
          <Radio :label="2">AA</Radio>
          <Radio :label="3">AAA</Radio>
          <Radio :label="4">AAAA</Radio>
          <Radio :label="5">AAAAA</Radio>
        </RadioGroup>
        <span v-else>{{level}}</span>
      </Col>
      <Col span="12">
        <label class="label">门票价格：</label>
        <InputNumber v-if="isEdit" :min="0" :step="0.5" v-model="form.price"></InputNumber>
        <span v-else>{{data.price}}</span>
        元 / 人
      </Col>
    </Row>
    <br>
    <Row :gutter="24">
      <Col span="12">
        <label class="label">排名：</label>
        第
        <InputNumber v-if="isEdit" :min="1" :step="1" v-model="form.ranking"></InputNumber>
        <span v-else>{{data.ranking}}</span>
        名
      </Col>
      <Col span="12">
        <label class="label">推荐度：</label>
        <Rate v-if="isEdit" show-text allow-half clearable :count="5" v-model="form.recommendLevel">
          <span style="color: #f5a623">{{ form.recommendLevel }}</span>
        </Rate>
        <Rate v-else show-text allow-half disabled :count="5" v-model="data.recommendLevel">
          <span style="color: #f5a623">{{ data.recommendLevel }}</span>
        </Rate>
      </Col>
    </Row>
    <br>
    <Row :gutter="24">
      <Col span="12">
        <label class="label">描述：</label><br>
        <Input v-if="isEdit" :style="{width: '80%'}" v-model="form.description" type="textarea" :autosize="{minRows: 2,maxRows: 4}" placeholder="描述..." />
        <span v-else>{{ data.description }}</span>
      </Col>
      <Col span="12">
        <label class="label">公交路线：</label><br>
        <template v-if="isEdit">
          <Input :style="{width: '80%'}" v-for="(item, index) of form.busRoutes" :key="index" v-model="form.busRoutes[index]" placeholder="公交路线..." clearable />
        </template>
        <template v-else>
          <span v-for="(item, index) of data.busRoutes" :key="index">{{item}}<br></span>
        </template>
      </Col>
    </Row>
    <br>
    <Row :gutter="24">
      <Col span="12">
        <label class="label">小编评价：</label><br>
        <Input v-if="isEdit" :style="{width: '80%'}" v-model="form.editorComments" type="textarea" :autosize="{minRows: 1,maxRows: 3}" placeholder="小编评价..." />
        <span v-else>{{ data.editorComments }}</span>
      </Col>
      <Col span="12">
        <label class="label">其他图片：</label>
        <div class="mainImg" v-for="item of otherList">
          <img :src="item.url">
          <div class="mainList-cover">
            <Icon type="ios-eye-outline" @click.native="view(item.url)"></Icon>
            <Icon v-if="isEdit" type="ios-trash-outline" @click.native="otherRemove(item)"></Icon>
          </div>
        </div>
        <Upload
          v-if="isEdit"
          action="/file/upload"
          type="drag"
          ref="otherUpload"
          :show-upload-list="false"
          :default-file-list="defaultOtherList"
          :on-success="otherSuccess"
          :format="['jpg','jpeg','png']"
          :max-size="2048"
          :on-format-error="handleFormatError"
          :on-exceeded-size="handleMaxSize"
          :before-upload="otherBeforeUpload"
          style="display: inline-block;width:58px;">
          <div style="width: 58px;height:58px;line-height: 58px;">
            <Icon type="ios-camera" size="20"></Icon>
          </div>
        </Upload>
      </Col>
    </Row>
    <br>
    <Row :gutter="24">
      <Col span="12">
        <label class="label">主要图片：</label>
        <div class="mainImg" v-for="item of mainList">
            <img :src="item.url">
            <div class="mainList-cover">
              <Icon type="ios-eye-outline" @click.native="view(item.url)"></Icon>
              <Icon v-if="isEdit" type="ios-trash-outline" @click.native="mainRemove(item)"></Icon>
            </div>
        </div>
        <Upload
          v-if="isEdit"
          action="/file/upload"
          type="drag"
          ref="mainUpload"
          :show-upload-list="false"
          :default-file-list="defaultMainList"
          :on-success="mainSuccess"
          :format="['jpg','jpeg','png']"
          :max-size="2048"
          :on-format-error="handleFormatError"
          :on-exceeded-size="handleMaxSize"
          :before-upload="mainBeforeUpload"
          style="display: inline-block;width:58px;">
          <div style="width: 58px;height:58px;line-height: 58px;">
            <Icon type="ios-camera" size="20"></Icon>
          </div>
        </Upload>
      </Col>
    </Row>
    <Modal title="View Image" v-model="visible">
      <img :src="viewImgUrl" v-if="visible" style="width: 100%">
    </Modal>
    <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
  import axios from 'axios'
  import { mapState } from 'vuex'
    export default {
      name: "Item",
      props: {
        active: String
      },
      data() {
        return {
          isAdd: false, // false为修改 true为新增
          isEdit: false, // 是否编辑状态
          visible: false,
          spinShow: false,
          viewImgUrl: '',
          data: {}, // 原始数据
          form: {}, // 表单数据
          mainList: [],
          otherList: [],
          defaultMainList: [],
          defaultOtherList: []
        }
      },
      computed: {
        level() {
          switch (this.data.travelLevel) {
            case 1:
              return 'A'
            case 2:
              return 'AA'
            case 3:
              return 'AAA'
            case 4:
              return 'AAAA'
            case 5:
              return 'AAAAA'
            default:
              return ''
          }
        },
        ...mapState(['types'])
      },
      methods: {
        view(url) {
          this.viewImgUrl = url
          this.visible = true
        },
        mainRemove(file) {
          this.mainList = []
        },
        otherRemove(file) {
          let index = this.otherList.indexOf(file)
          this.otherList.splice(index, 1)
        },
        mainBeforeUpload() {
          if (this.mainList.length === 1) {
            this.$Notice.warning({
              title: '只能上传一张主要图片！'
            })
            return false
          }
          return true
        },
        otherBeforeUpload() {
          if (this.otherList.length === 5) {
            this.$Notice.warning({
              title: '最多上传五张其他图片！'
            })
            return false
          }
          return true
        },
        mainSuccess(res, file, filelist) {
          this.mainList = []
          this.mainList.push(res.data)
        },
        otherSuccess(res, file, filelist) {
          this.otherList.push(res.data)
        },
        handleFormatError(file, filelist) {
          this.$Notice.warning({
            title: '只能上传jpg、png格式的图片！'
          })
        },
        handleMaxSize (file, filelist) {
          this.$Notice.warning({
            title: '图片大小不得超过2M！'
          })
        },
        getData(name) {
          let params = {name: name}
          this.spinShow = true
          axios.post('/travel/getTravelDetailByName', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.data = data.data
                this.initList()
                this.spinShow = false
              }
            })
        },
        initList() {
          this.defaultMainList = []
          this.defaultMainList.push({
            name: '',
            url: this.data.mainPictureUrl
          })
          this.defaultOtherList = []
          this.data.otherPictureUrls.map(val => {
            this.defaultOtherList.push({
              name: '',
              url: val
            })
          })
          this.mainList = this.defaultMainList
          this.otherList = this.defaultOtherList
        },
        edit() {
          this.isAdd = false
          // 深拷贝
          this.form = JSON.parse(JSON.stringify(this.data))
          // price转数字
          this.form.price = this.form.price - 0
          // busRoutes设置length为3
          let arr = []
          for (let i = 0; i < 3; i++) {
            if (this.form.busRoutes[i]) {
              arr.push(this.form.busRoutes[i])
            } else {
              arr.push('')
            }
          }
          this.form.busRoutes = arr
          this.isEdit = true
        },
        add() {
          this.isAdd = true
          this.form = {
            name: '',
            type: '',
            travelLevel: 0,
            price: 0,
            ranking: 0,
            recommendLevel: 0,
            busRoutes: ['', '', ''],
            editorComments: '',
            description: '',
            mainPictureUrl: '',
            otherPictureUrls: []
          }
          this.defaultMainList = []
          this.defaultOtherList = []
          this.mainList = this.defaultMainList
          this.otherList = this.defaultOtherList
          this.isEdit = true
        },
        deleteData(id) {
          let params = {
            id: id
          }
          axios.post('/travel/deleteTravelInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$Modal.remove()
                this.$Notice.success({
                  title: '删除成功！'
                })
                this.refresh('')
              }
            })
        },
        remove() {
          let id = this.data.id
          this.$Modal.confirm({
            title: '是否删除该景点？',
            loading: true,
            onOk: () => {
              this.deleteData(id)
            }
          })
        },
        updateData(form) {
          let params = form
          axios.post('/travel/updateTravelInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$Modal.remove()
                this.$Notice.success({
                  title: '修改成功！'
                })
                this.refresh(form.name)
                // 未修改name时不会重新获取数据，需要主动调接口
                if (form.name === this.data.name) {
                  this.isEdit = false
                  this.getData(form.name)
                }
              }
            })
        },
        addData(form) {
          let params = form
          axios.post('/travel/insertTravelInfo', params)
            .then((res) => {
              const data = res.data
              if (data.code === 70000) {
                this.$Modal.remove()
                this.$Notice.success({
                  title: '新增成功！'
                })
                this.refresh(form.name)
              }
            })
        },
        checkData(form) {
          if (!form.name.trim()) {
            return false
          }
          if (!form.type.trim()) {
            return false
          }
          if ([1,2,3,4,5].indexOf(form.travelLevel) === -1) {
            return false
          }
          if (form.price < 0) {
            return false
          }
          if (form.ranking < 1) {
            return false
          }
          if (form.busRoutes.length === 0) {
            return false
          }
          if (!form.editorComments.trim()) {
            return false
          }
          if (!form.description.trim()) {
            return false
          }
          if (!form.mainPictureUrl.trim()) {
            return false
          }
          if (form.otherPictureUrls.length === 0) {
            return false
          }
          return true
        },
        save() {
          // 处理表单form
          let bus = JSON.parse(JSON.stringify(this.form.busRoutes)) //记录一下
          let arr = []
          this.form.busRoutes.map(val => {
            if (val.trim()) {
              arr.push(val)
            }
          })
          this.form.busRoutes = arr
          this.form.mainPictureUrl = this.mainList[0] ? this.mainList[0].url : ''
          let array = []
          for (let i = 0; i < this.otherList.length; i++) {
              array.push(this.otherList[i].url)
          }
          this.form.otherPictureUrls = array
          // 校验表单完整性
          if (this.checkData(this.form)) {
            this.$Modal.confirm({
              title: '是否保存？',
              onOk: () => {
                if (this.isAdd) {
                  this.addData(this.form)
                } else {
                  this.updateData(this.form)
                }
              }
            })
          } else {
            this.form.busRoutes = bus // 复原
            this.$Notice.warning({
              title: '表单未填写完毕！'
            })
          }
        },
        cancel() {
          this.$Modal.confirm({
            title: '取消不会保存，是否继续？',
            onOk: () => {
              this.initList()
              this.isEdit = false
            }
          })
        },
        refresh(name) {
          this.$emit('refresh', name)
        }
      },
      watch: {
        active(val) {
          if (val) {
            this.isEdit = false
            this.getData(val)
          }
        }
      },
      mounted() {
        if (this.active) {
          this.getData(this.active)
        }
      }
    }
</script>

<style lang="stylus" scoped>
.item
  position relative
  .label
    margin-right 18px
  .mainImg:hover .mainList-cover
    display block
  .mainImg
    display inline-block
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0,0,0,.2);
    margin-right: 4px;
    img
      width: 100%;
      height: 100%;
    .mainList-cover
      display: none;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0,0,0,.6);
      i
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
</style>



