<template>
  <div class="show">
    <Layout>
      <Header :style="{height: '10vh', lineHeight: '10vh', position: 'fixed', width: '100%', zIndex: '9'}">
        <div class="show-title">
          云南旅游景点
          <div class="user-info">
            <Icon :style="{cursor: 'pointer'}" :title="'去管理页'" @click="goIndex()" type="md-swap" />
          </div>
        </div>
      </Header>
      <Content :style="{height: '90vh', position: 'relative', marginTop: '10vh', overflowY: 'auto'}">
        <div class="wrapper">
          <Row :gutter="20"
               v-for="(data, index) of datas"
               :key="index"
               style="margin-top: 20px; margin-bottom: 20px;">
            <Col span="6"
                 v-for="(item, index) of data"
                 :key="item.id">
              <Card>
                <p slot="title">{{item.name}}</p>
                <a href="#" slot="extra" @click.prevent="showMore(item, index)">more</a>
                <Badge :count="item.otherPictureUrls.length + 1"
                       type="info"
                       :offset="[140,20]"
                       style="display: block;">
                  <div class="img"
                       @click="openImg(item)"
                       :style="{backgroundImage: 'url(' + item.mainPictureUrl + ')'}">
                  </div>
                </Badge>
              </Card>
            </Col>
          </Row>
          <Row style="text-align: center">
            <Page :total="total"
                  :current="currentPage"
                  @on-change="changePage"
                  size="small"
                  :page-size="8"
                  show-total />
          </Row>
        </div>
        <Spin size="large" fix v-if="spinShow"></Spin>
      </Content>
    </Layout>
    <Drawer :closable="false"
            width="30"
            :transfer="false"
            :inner="true"
            :title="data.name"
            v-model="showDrawer"
            :placement="position">
      <Row>
        <Col span="24">
          <label class="label">类型：</label>
          <span>{{data.type}}</span>
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">等级：</label>
          <span>{{level}}</span>
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">门票价格：</label>
          <span>{{data.price}}</span>
          元 / 人
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">排名：</label>
          第
          <span>{{data.ranking}}</span>
          名
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">推荐度：</label>
          <Rate show-text allow-half disabled :count="5" v-model="data.recommendLevel">
            <span style="color: #f5a623">{{ data.recommendLevel }}</span>
          </Rate>
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">描述：</label><br>
          <span>{{ data.description }}</span>
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">公交路线：</label><br>
          <span v-for="(item, index) of data.busRoutes" :key="index">{{item}}<br></span>
        </Col>
      </Row>
      <br>
      <Row>
        <Col span="24">
          <label class="label">小编评价：</label><br>
          <span>{{ data.editorComments }}</span>
        </Col>
      </Row>
      <br>
    </Drawer>
    <show-image v-if="open" @isImageShow="isImageShow" :imgs="imgs"></show-image>
  </div>
</template>

<script>
  import ShowImage from './Image'
  import axios from 'axios'
  const count = 8
  const num = 4
  export default {
    name: "Show",
    components: {
      ShowImage
    },
    data() {
      return {
        open: false,
        spinShow: false,
        showDrawer: false,
        position: 'right',
        currentPage: 1,
        total: 0,
        datas: [],
        data: [],
        imgs:[]
      }
    },
    computed: {
      level() {
        switch (this.data.travelLevel) {
          case 1:
            return 'A'
          case 2:
            return 'AA'
          case 3:
            return 'AAA'
          case 4:
            return 'AAAA'
          case 5:
            return 'AAAAA'
          default:
            return ''
        }
      }
    },
    methods: {
      goIndex() {
        this.$Modal.confirm({
          title: '是否跳转到管理页？',
          onOk: () => {
            this.$router.push('/index')
          }
        })
      },
      isImageShow(flag) {
        this.open = flag
      },
      openImg(item) {
        let arr = []
        arr.push(item.mainPictureUrl)
        item.otherPictureUrls.map((val) => {
          arr.push(val)
        })
        this.imgs = arr
        if (this.imgs.length > 0) {
          this.open = true
        }
      },
      showMore(item, index) {
        this.data = item
        index / 2 < 1 ? this.position = 'right' : this.position = 'left'
        this.showDrawer = true
      },
      changePage(page) {
        this.currentPage = page
        this.getDatas(this.currentPage)
      },
      getDatas(currentPage) {
        let params = {
          sort: "ranking",
          page: currentPage,
          count
        }
        this.spinShow = true
        axios.post('/travel/getHomePageInfo', params)
          .then((res) => {
            const data = res.data
            if (data.code === 70000) {
              this.total = data.data.count
              this.datas = this.computeDatas(data.data.homePageInfos)
              this.spinShow = false
            }
          })
      },
      computeDatas(infos) {
        let rowNum = Math.ceil(infos.length / num)
        let arr = []
        for (let i = 0; i < rowNum; i++) {
          arr.push([])
        }
        for (let i = 0; i < infos.length; i++) {
          let index = Math.floor(i / num)
          arr[index].push(infos[i])
        }
        return arr
      }
    },
    mounted() {
      this.getDatas(this.currentPage)
    }
  }
</script>

<style lang="stylus" scoped>
.show >>> .ivu-layout-content::-webkit-scrollbar
  display none
.show >>> .ivu-drawer-body::-webkit-scrollbar
  display none
.show
  position relative
  overflow hidden
  background #f5f7f9
  .show-title
    color #eee
    font-size 24px
  .user-info
    display inline-block
    float right
  .wrapper
    margin 0 auto
    max-width 1280px
    .img
      width 100%
      height 160px
      border-radius 6px
      border 1px solid #eee
      background-size cover
      background-position center
      cursor pointer
  .label
    margin-right 10px
</style>




